import React, {Component} from "react";
import TooltipFromHtml from "eshop/modules/core/components/ui/TooltipFromHtml";
import {Feature} from "eshop/modules/configurator/components/lp/offers/newOfferBoxComponents/Feature";

export class PriceBox extends Component {

    mapFeaturesWithPositions(){
        let featuresPositions=this.props.featuresPositions;
        let featuresGroup=this.props.featuresGroup;
        let positioned=[];
        featuresPositions.forEach(f => {
            let feature=featuresGroup.find(feat => feat.code==f);
            if(feature)
                positioned.push(feature);
            else
                positioned.push({});
        });
        return positioned;
    }

    render() {
        var price = this.props.price && this.props.price.split(',');
        var integerPricePart = price && price[0];
        var fractionPricePart = price && price[1];

        const style = {
            "minWidth": "100%"
        };
        let featuresGroup=this.mapFeaturesWithPositions();
        return (
        <div className="u-padding-l">
            <div key={"div1_"+this.props.id} className="u-spacing-l">
                <div className="js-layout-rateplan-name-fixer u-animate-height">
                    <div className="js-height-sensitive-element">
                        <p>{this.props.name}</p>
                    </div>
                </div>
            </div>
            <div className="u-clearfix">
                <div style={style} className="u-right u-small-right u-text-left u-text-nowrap">
                    <div className="u-table u-margin-minus-right-l u-small-margin-minus-right">
                        <div className="u-table-cell">
                            <span className="opl-price-v2 opl-price-v2--l" data-price={this.props.price}>
                                <span className="opl-price-v2__part">
                                    <span className="opl-price-v2__whole">{integerPricePart}</span>
                                </span>
                                <span className="opl-price-v2__part">
                                    <span className="opl-price-v2__separator">,</span>
                                    <span className="opl-price-v2__decimal">{fractionPricePart}</span>
                                    <span className="opl-price-v2__suffix">zł/mies.</span>
                                </span>
                            </span>
                        </div>
                        <div className="u-table-cell u-vertical-top">
                            {
                                this.props.oldPrice && this.props.tooltipValues
                                ? TooltipFromHtml.conditionalRender(this.props.tooltipValues['convergent'])
                                : TooltipFromHtml.conditionalRender(this.props.tooltipValues['noConvergent'])
                            }
                        </div>
                    </div>
                </div>
            </div>
            <div className="js-layout-fixer-group-1">
                <div className="js-height-sensitive-element">
                    {
                        this.props.oldPrice && <p className="u-padding-top u-font-bold g-gray5-c u-text-line-through">{this.props.oldPrice + " zł/mies."}</p>
                    }
                </div>
            </div>

            {
               featuresGroup.map((feature,index) =>
                <div key={"Feature_"+index+"_"+this.props.id} className={"js-layout-fixer-group-"+(2+index)+" feature"+index}>
                    <div className="js-height-sensitive-element">
                        {feature.code&&
                            <Feature
                                key={feature.code + this.props.id}
                                removeClassFromLast={true}
                                feature={feature}
                                className="u-padding-top-l"
                                processType={this.props.processType}
                                convergence={this.props.convergence}
                            />}
                        </div>
                </div>
               )
            }

            <div className="js-layout-fixer-group-4">
                <div className="js-height-sensitive-element">
                </div>
            </div>
        </div>
        )
    }
}